<template>
  <div class="zk-main"
      v-loading="loading"
      :element-loading-text="loadingText">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus" type="warning" @click="addInstance()" size="small">{{$t('common.add') }}</el-button>
      <div class="inline-block ml-10" v-if="batchBtns">
        <el-button icon="fa fa-plane" type="info" size="small" @click="zkInstall()">部署</el-button>
        <el-button icon="fa fa-play" type="info" size="small" @click="startZk()">启动</el-button>
        <el-button icon="fa fa-link" type="warning" size="small" @click="zkUninstall()">卸载</el-button>
        <el-button icon="fa fa-stop" type="warning" size="small" @click="stopZk()">停止</el-button>
        <el-button icon="fa fa-trash" type="danger" size="small" @click="deleteInstance()">{{$t('common.delete') }}</el-button>
      </div>
    </div>
    <el-form label-width="85px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="集群名称：">
            <el-select v-model="zkListForm.clusterId">
              <el-option label="请选择" value=""></el-option>
              <el-option v-for="item in clusterOption" :key="item.pkValue" :label="item.pkDesc"
                         :value="item.pkValue"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="实例名称：">
            <el-input v-model.trim="zkListForm.instanceName"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="安装路径：">
            <el-input v-model.trim="zkListForm.installPath"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" icon="el-icon-search" @click="searchZkList" size="small">{{$t('common.search') }}
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格数据 -->
    <el-table
      v-loading="tableLoading"
      @selection-change="handleSelectionChange" :data="zkInfo.list" stripe>
      <el-table-column
        type="selection"
        align="center"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        align="center"
        label="实例名称"
        width="150">
      </el-table-column>
      <el-table-column
        label="状态"
        align="center"
        width="150">
        <template slot-scope="props">
          <span :style="props.row.status | filterAppStatusColor">
            <i v-if="props.row.status !=='UNINSTALL'" class="fa fa-lightbulb-o name-state"></i>
            {{props.row.status | filterAppStatus}}
          </span>
          <el-tooltip content="刷新状态" placement="top">
            <el-button type="text" size="small" @click="refresh(props.row, props.$index)" icon="fa fa-refresh"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column
        prop="clusterName"
        align="center"
        label="集群名称"
        width="120">
      </el-table-column>
      <el-table-column
        prop="ip"
        align="center"
        label="IP"
        width="150">
      </el-table-column>
      <el-table-column
        label="配置信息"
        width="60"
        align="center">
        <template slot-scope="props">
          <el-button type="text" size="small" @click="openConfig(props.row)" icon="fa fa-cog"></el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="节点数据"
        width="60"
        align="center">
        <template slot-scope="props">
          <el-button type="text" size="small" @click="openNode(props.row)" icon="fa fa-database"></el-button>
        </template>
      </el-table-column>
      <!-- 监控 -->
      <el-table-column
        align="center"
        :label="$t('common.monitor')"
        width="70">
        <template slot-scope="props">
          <el-button type="text" size="small" @click="moniter(props)" icon="fa fa-bar-chart"></el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="installPath"
        align="center"
        label="安装路径"
        show-overflow-tooltip
        width="150">
      </el-table-column>
      <el-table-column
        prop="installName"
        label="安装名"
        width="100"
        align="center">
      </el-table-column>
      <el-table-column
        label="角色"
        width="100"
        align="center">
        <template slot-scope="props">
          <div v-if="props.row.type !== ''">
            <span>{{props.row.type | filterRole}}</span>
            <el-tooltip content="刷新状态" placement="top">
              <el-button type="text" size="small" @click="refreshRole(props.row, props.$index)" icon="fa fa-refresh"></el-button>
            </el-tooltip>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label="服务器日志"
        width="80"
        align="center">
        <template slot-scope="props">
          <el-button type="text" size="small" icon="fa fa-file-text-o" @click="viewLog(props.row)"></el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="versionNo"
        label="版本号"
        width="100"
        align="center">
      </el-table-column>
      <el-table-column
        prop="remark"
        label="描述"
        align="center"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        width="120"
        align="center"
        :label="$t('common.operation')"
        fixed="right">
        <template slot-scope="props">
          <el-dropdown trigger="click">
            <el-button type="text" size="mini">
              操作<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'UNINSTALL'" type="text" size="mini" icon="fa fa-pencil-square-o" @click="addInstance(props.row)">修改</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'UNINSTALL'" type="text" size="mini" icon="fa fa-plane" @click="zkInstall(props.row)">部署</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'INSTALL' || props.row.status === 'STOPPED'" type="text" size="mini" icon="fa fa-link" @click="zkUninstall(props.row)">卸载</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'INSTALL' || props.row.status === 'STOPPED'" type="text" size="mini" icon="fa fa-play" @click="startZk(props.row)">启动</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.status === 'RUNNING' || props.row.status === 'INSTALL'" type="text" size="mini" icon="fa fa-stop" @click="stopZk(props.row)">停止</el-button>
              </el-dropdown-item>
              <el-dropdown-item divided v-if="props.row.status === 'UNINSTALL'">
                <el-button type="text" size="mini" class="btn-text-red" @click="deleteInstance(props.row)" icon="fa fa-trash">删除</el-button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="zkInfo.total">
    </el-pagination>
    <!-- 添加zk实例 -->
    <el-dialog
      :title="instanceTitle"
      :visible.sync="instanceVisible"
      width="35%"
      @close="instanceCancel('addInstanceForm')"
      center>
      <el-form :model="addInstanceForm" :rules="addInstanceRules" ref="addInstanceForm" label-width="80px"
               size="small">
        <el-form-item label="" prop="status">
          <el-radio v-model="addInstanceForm.status" label="UNINSTALL">未部署</el-radio>
          <el-radio v-model="addInstanceForm.status" label="INSTALL">已部署</el-radio>
        </el-form-item>
        <el-form-item label="集群名称" prop="clusterId">
          <el-select class="block" v-model="addInstanceForm.clusterId">
            <el-option label="请选择" value=""></el-option>
            <el-option v-for="(item, index) in clusterOption" :key="index" :label="item.pkDesc"
                       :value="item.pkValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="IP地址" prop="ip">
          <el-select class="block" v-model="addInstanceForm.ip">
            <el-option v-for="(item, index) in selectIp" :key="index" :label="item.pkDesc" :value="item.pkValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="实例名称" prop="name">
          <el-input v-model.trim="addInstanceForm.name" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="安装路径" prop="installPath">
          <el-input v-model.trim="addInstanceForm.installPath" maxlength="200"></el-input>
        </el-form-item>
        <el-form-item label="安装名" prop="installName">
          <el-input v-model.trim="addInstanceForm.installName" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="版本号" prop="versionNo">
          <el-select class="block" v-model="addInstanceForm.versionNo">
            <el-option v-for="(item, index) in selectVersion" :key="index" :label="item.pkDesc"
                       :value="item.pkValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="描述">
          <el-input type="textarea" v-model.trim="addInstanceForm.remark" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="instanceOk('addInstanceForm')" size="small">{{ $t('common.ok') }}</el-button>
        <el-button @click="instanceCancel('addInstanceForm')" size="small">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
    <el-dialog
      :title="configTitle"
      :visible.sync="configVisible"
      width="30%"
      center
      @close="closeConfig">
      <el-form :model="configForm" ref="configForm" label-width="100px">
        <el-form-item label="客户端端口：">
          <el-input v-model.trim="configForm.clientPort" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item label="通信端端口：">
          <el-input v-model.trim="configForm.commPort" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item label="选举端端口：">
          <el-input v-model.trim="configForm.electPort" :disabled="configDisabled"></el-input>
        </el-form-item>
        <el-form-item label="myId：">
          <el-input v-model.trim="configForm.myid" :disabled="configDisabled"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="closeConfig" size="small">{{ $t('common.cancel') }}</el-button>
        <el-button type="primary" @click="configConfirm" size="small" :disabled="configDisabled">{{ $t('common.ok') }}</el-button>
      </span>
    </el-dialog>
    <zk-log v-if="logVisible" :logParams="logParams" />
    <zk-node v-if="nodeVisible" :params="nodeParams" />
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import Api from '../../constants/Api'
  import {SUCCESS_CODE} from '../../constants/constant'
  import zkLog from './zk-log'
  import zkNode from './zk-node'

  export default {
    components: {
      zkLog,
      zkNode
    },
    data() {
      return {
        currentPage: 1,
        pageSize: 10,
        instanceVisible: false,
        instanceTitle: '',
        selectTableList: [],
        zkListForm: {
          clusterId: '',
          instanceName: '',
          installPath: ''
        },
        addInstanceForm: {},
        addInstanceRules: {
          ip: [
            {required: true, message: '请选择IP地址', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '请输入实例名称', trigger: 'blur'}
          ],
          installPath: [
            {required: true, message: '请输入安装路径', trigger: 'blur'}
          ],
          installName: [
            {required: true, message: '请输入安装名', trigger: 'blur'}
          ],
          versionNo: [
            {required: true, message: '请选择版本号', trigger: 'blur'}
          ]
        },
        configVisible: false,
        configTitle: '',
        configForm: {},
        logParams: {},
        logVisible: false,
        nodeVisible: false,
        nodeParams: {},
        loading: false,
        loadingText: '',
        tableLoading: false,
        configDisabled: false
      }
    },
    activated() {
      const vm = this
      vm.getClusterOption()
      vm.getIpOption()
      vm.getVersionOption()
      if (vm.$route.params.id && Object.prototype.toString.call(vm.$route.params.id) === '[object Number]') {
        vm.zkListForm.clusterId = (vm.$route.params.id).toString() || ''
      }
      this.searchZkList()
    },
    computed: {
      ...mapState({
        clusterOption: state => state.zookeeper.clusterOption,
        zkInfo: state => state.zookeeper.zkInfo,
        selectIp: state => state.zookeeper.selectIp,
        selectVersion: state => state.zookeeper.selectVersion
      }),
      batchBtns() {
        return this.selectTableList.length
      }
    },
    methods: {
      ...mapActions('zookeeper', ['getClusterOption', 'getIpOption', 'getVersionOption', 'getZkList', 'operationTable', 'getZkListConfig', 'updateZkListConf', 'updateZkStatus', 'updateZkRole']),
      handleSelectionChange(zookeeper) {
        this.selectTableList = zookeeper
      },
      handleCurrentChange(val) {
        this.currentPage = val
        this.searchZkList()
      },
      searchZkList() {
        const vm = this
        const params = {
          start: vm.currentPage,
          length: vm.pageSize,
          name: vm.zkListForm.instanceName,
          installPath: vm.zkListForm.installPath,
          clusterId: vm.zkListForm.clusterId
        }
        vm.getZkList({params, vm})
      },
      addInstance(data) {
        const vm = this
        if (data && data.moId) {
          vm.instanceTitle = '修改zk实例'
          vm.addInstanceForm = {...data}
          vm.addInstanceForm.clusterId && (vm.addInstanceForm.clusterId = vm.addInstanceForm.clusterId.toString())
          vm.instanceVisible = true
        } else {
          vm.instanceTitle = '添加zk实例'
          vm.addInstanceForm = {
            status: 'UNINSTALL',
            groupName: '',
            clusterName: ''
          }
          vm.instanceVisible = true
        }
      },
      instanceCancel(formName) {
        const vm = this
        vm.instanceVisible = false
        vm.$refs[formName].clearValidate()
      },
      deleteInstance(props) {
        this.handleZookeeperInstance(props, Api.zkListDelete, '删除')
      },
      instanceOk(formName) {
        const vm = this
        vm.$refs[formName].validate(valid => {
          if (valid) {
            if (!vm.addInstanceForm.moId) {
              vm.operationTable({
                url: Api.zkListAdd,
                data: vm.addInstanceForm,
                callback: () => {
                  vm.$message({
                    type: 'success',
                    message: '添加成功'
                  })
                  vm.instanceCancel('addInstanceForm')
                  vm.searchZkList()
                },
                callbackError: (error) => {
                  vm.$message({
                    type: 'error',
                    message: (error.data && error.data.message) || '添加失败'
                  })
                }
              })
            } else {
              vm.operationTable({
                url: Api.zkListUpdate,
                data: vm.addInstanceForm,
                callback: () => {
                  vm.$message({
                    type: 'success',
                    message: '修改成功'
                  })
                  vm.instanceCancel('addInstanceForm')
                  vm.searchZkList()
                },
                callbackError: (error) => {
                  vm.$message({
                    type: 'error',
                    message: (error.data && error.data.message) || '修改失败'
                  })
                }
              })
            }
          }
        })
      },
      async openConfig(props) {
        if (props.status !== 'UNINSTALL') {
          this.configDisabled = true
        } else {
          this.configDisabled = false
        }
        const params = {
          moId: props.moId
        }
        const res = await this.getZkListConfig(params)
        if (res && res.code === SUCCESS_CODE) {
          const resData = JSON.parse(res.data)
          this.configForm = resData
        }
        this.configTitle = props.name + '的配置信息'
        this.configVisible = true
      },
      closeConfig() {
        this.configVisible = false
      },
      configConfirm() {
        this.updateZkListConf({
          data: this.configForm,
          callback: () => {
            this.$message.success('配置更新成功')
            this.closeConfig()
          },
          callbackError: (error) => {
            this.$message.error((error && error.data && error.data.message) || '配置更新失败')
          }
        })
      },
      openNode(props) {
        this.nodeParams = props
        this.nodeVisible = true
      },
      viewLog(props) {
        this.logParams = props
        this.logVisible = true
      },
      zkInstall(props = {}) {
        this.handleZookeeperInstance(props, Api.zkListinstall, '部署')
      },
      zkUninstall(props = {}) {
        this.handleZookeeperInstance(props, Api.zkListUninstall, '卸载')
      },
      startZk(props = {}) {
        this.handleZookeeperInstance(props, Api.zkListStart, '启动')
      },
      stopZk(props = {}) {
        this.handleZookeeperInstance(props, Api.zkListStop, '停止')
      },
      handleZookeeperInstance(props, url, opText) {
        const vm = this
        if (props && props.moId) {
          vm.$confirm(`确认${opText}${props.name}？`, opText, {
            confirmButtonText: vm.$t('common.ok'),
            cancelButtonText: vm.$t('common.cancel'),
            type: 'warning',
            center: true
          }).then(() => {
            const params = {
              zkIds: props.moId
            }
            vm.loading = true
            vm.loadingText = `${opText}中...`
            vm.operationTable({
              url: url,
              data: params,
              callback: () => {
                vm.searchZkList()
                vm.loading = false
                vm.$message.success(`${opText}成功`)
              },
              callbackError: (error) => {
                vm.loading = false
                vm.$message.error((error && error.data && error.data.message) || `${opText}失败`)
              }
            })
          }).catch(() => {})
        } else {
          vm.$confirm(`确认${opText}所选中实例？`, opText, {
            confirmButtonText: vm.$t('common.ok'),
            cancelButtonText: vm.$t('common.cancel'),
            type: 'warning',
            center: true
          }).then(() => {
            const idList = []
            const nameList = []
            vm.selectTableList.forEach(item => {
              idList.push(item.moId)
              if (opText === '删除' && item.status !== 'UNINSTALL') {
                nameList.push(item.name)
              }
              if (opText === '部署' && item.status !== 'UNINSTALL') {
                nameList.push(item.name)
              }
              if (opText === '启动' && (item.status === 'RUNNING' || item.status === 'UNINSTALL')) {
                nameList.push(item.name)
              }
              if (opText === '停止' && (item.status === 'STOPPED' || item.status === 'UNINSTALL')) {
                nameList.push(item.name)
              }
              if (opText === '卸载' && (item.status === 'UNINSTALL' || item.status === 'RUNNING')) {
                nameList.push(item.name)
              }
            })
            if (nameList.length > 0) {
              let nameStr = nameList.join('、')
              vm.$message({
                type: 'warning',
                message: `实例名称：${nameStr}不符合该操作，请重新选择`,
                duration: 5000
              })
              return
            }
            vm.loading = true
            vm.loadingText = `${opText}中...`
            vm.operationTable({
              url: url,
              data: {zkIds: idList.toString()},
              callback: () => {
                vm.searchZkList()
                vm.loading = false
                vm.$message.success(`${opText}成功`)
              },
              callbackError: (error) => {
                vm.loading = false
                vm.$message.error((error && error.data && error.data.message) || `${opText}失败`)
              }
            })
          }).catch(() => {})
        }
      },
      refresh(props, index) {
        const params = { moId: props.moId }
        this.updateZkStatus({
          data: params,
          index
        })
      },
      refreshRole(props, index) {
        const params = { moId: props.moId }
        this.updateZkRole({
          data: params,
          index
        })
      },
      // 监控
      moniter(scope) {
        let query = {
          ip: scope.row.ip,
          path: scope.row.installPath + '/' + scope.row.installName
        }
        this.$router.push({name: 'zkEcharts', query: query, params: {'id': scope.row.ip}})
      }
    },
    filters: {
      filterRole(value) {
        let text = ''
        switch (value) {
          case '0':
            text = '领导者'
            break
          case '1':
            text = '跟随者'
            break
          case '2':
            text = '观察者'
            break
          case '-1':
            text = '未知'
            break
          default:
            text = ''
        }
        return text
      }
    }
  }
</script>

<style lang="scss">
  @import "../../assets/common.scss";
  .zk-main {
    .el-icon-loading {
      font-size: 36px;
    }
  }
</style>



