<template>
  <div class="zk-node">
    <el-dialog :title="`${params.name}：${params.ip}节点数据`" :visible.sync="$parent.nodeVisible" width="50%" center @close="closeNode">
      <el-form :model="form" ref="form" label-width="80px" size="small">
        <el-row :gutter="15">
          <el-col :span="6">
            <el-input v-model.trim="params.ip" disabled></el-input>
          </el-col>
          <el-col :span="9">
            <el-form-item label="节点：">
              <el-input v-model.trim="form.nodeName" @keyup.13.native="searchNode(form.nodeName)" placeholder="请输入节点名称"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2" class="text-left">
            <el-button type="primary" icon="el-icon-search" @click="searchNode(form.nodeName)" size="small">{{$t('common.search') }}
            </el-button>
          </el-col>
        </el-row>
      </el-form>
      <div class="node-content">
        <el-tree
          :data="zookeeperNodes"
          node-key="id"
          :props="defaultProps"
          :filter-node-method="filterNode"
          ref="zkTree">
        </el-tree>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  data() {
    return {
      form: {},
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  props: {
    params: Object
  },
  computed: {
    ...mapState({
      zookeeperNodes: state => state.zookeeper.zookeeperNodes
    })
  },
  created() {
    this.getZkListNode({ipPort: `${this.params.ip}:${this.params.clientPort}`})
  },
  methods: {
    ...mapActions('zookeeper', ['getZkListNode']),
    filterNode(value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    filterText(val) {
      this.$refs.zkTree.filter(val)
    },
    closeNode() {
      this.$parent.nodeVisible = false
    },
    searchNode(val) {
      this.$refs.zkTree.filter(val)
    },
    handleNodeClick(data) {
      console.log(data)
    }
  }
}
</script>

<style lang="scss">
.node-content {
  border: 1px solid #ebebeb;
  padding: 15px 0;
  overflow-x: auto;
  .el-tree-node>.el-tree-node__children {
    overflow: auto;
  }
}
</style>



