<template>
  <div class="log-modal">
    <el-dialog
      :title="logParams.name + '日志'"
      :visible.sync="$parent.logVisible"
      width="60%"
      center
      @close="closeLog">
      <div class="log-box">
        <p v-for="(item, index) in serverLog" v-text="item" :key="index"></p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  export default {
    data() {
      return {}
    },
    props: {
      logParams: Object
    },
    computed: {
      ...mapState({
        serverLog: state => state.zookeeper.serverLog
      })
    },
    created() {
      this.getZkServerLog({moId: this.logParams.moId, lines: 50})
    },
    methods: {
      ...mapActions('zookeeper', ['getZkServerLog']),
      closeLog() {
        this.$parent.logVisible = false
      }
    }
  }
</script>



